    height: 100vh;
    width: 100vw;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.2);
  }
}
.footer {
  background-color: var(--bg-footer);
}
.footer * {
  color: var(--text-footer) !important;
}
.footer__content {
  padding-top: 84px;
  height: 380px;
  display: flex;
  justify-content: space-between;
}
.footer__inn {
  display: flex;
  flex-direction: column;
}
.footer__links {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.footer__link {
  color: var(--text-footer);
}
.footer__socials {
  display: flex;
  gap: 18px;
}
:root {
  --text-p: #323232;
  --text-h: #57463A;
  --bg-main: #F3EFEA;
  --bg-footer: #715B4B;
  --text-footer: #F6F6F6;
  --menu-burger: #57463A;
}

#root {
  overflow: hidden;
  height: 100%;
  min-width: 375px;
}

body {
